// pages/index.js

import { useState } from "react";

export default function Home() {
  // ─── STATE HOOKS FOR FORM FIELDS ─────────────────────────────────────────
  // Checkbox fields (converted to 0 or 1 in payload):
  const [rainTomorrow, setRainTomorrow] = useState(false);
  const [rainToday, setRainToday] = useState(false);

  // Number spinners with their respective min/max constraints:
  const [rainfall, setRainfall] = useState(0);       // 0–371
  const [humidity3pm, setHumidity3pm] = useState(0); // 0–45
  const [cloud3pm, setCloud3pm] = useState(0);       // 0–8
  const [sunshine, setSunshine] = useState(0);       // 0–17

  // For displaying the API response (optional)
  const [apiResponse, setApiResponse] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");

  // ─── HANDLER FUNCTIONS ────────────────────────────────────────────────────
  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    setErrorMsg("");
    setApiResponse(null);

    // Build payload, converting booleans to integers (0/1)
    const payload = {
      RainTomorrow: rainTomorrow ? 1 : 0,
      Rainfall: rainfall,
      Humidity3pm: humidity3pm,
      RainToday: rainToday ? 1 : 0,
      Cloud3pm: cloud3pm,
      Sunshine: sunshine,
    };

    try {
      const res = await fetch("/api/predict", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(payload),
      });

      if (!res.ok) {
        throw new Error(`API Error: ${res.status} ${res.statusText}`);
      }

      const data = await res.json();
      setApiResponse(data);
    } catch (err) {
      console.error(err);
      setErrorMsg(err.message);
    } finally {
      setIsLoading(false);
    }
  };

  // ─── RENDER ────────────────────────────────────────────────────────────────
  return (
    <div className="min-h-screen flex flex-col">
      {/* Header (empty for now) */}
      <header className="bg-gray-800 text-white py-4">
        <h1 className="text-2xl text-center">Weather Risk Predictor</h1>
      </header>

      {/* Main content: two-column grid */}
      <main className="flex-1 container mx-auto px-4 py-8">
        <div className="grid grid-cols-2 gap-8">
          {/* ─── LEFT COLUMN: FORM ────────────────────────────────────────────── */}
          <div>
            <form
              onSubmit={handleSubmit}
              className="space-y-6 bg-white shadow-md rounded-lg p-6"
            >
              {/* Rain Tomorrow (checkbox) */}
              <div className="flex items-center">
                <input
                  type="checkbox"
                  id="rain-tomorrow"
                  name="rainTomorrow"
                  checked={rainTomorrow}
                  onChange={(e) => setRainTomorrow(e.target.checked)}
                  className="h-5 w-5 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                />
                <label
                  htmlFor="rain-tomorrow"
                  className="ml-2 block text-sm font-medium text-gray-700"
                >
                  Rain Tomorrow
                </label>
              </div>

              {/* Rainfall (spinner 0–371) */}
              <div>
                <label
                  htmlFor="rainfall"
                  className="block text-sm font-medium text-gray-700"
                >
                  Rainfall (0–371)
                </label>
                <div className="mt-1 flex items-center gap-4">
                  <input
                    type="number"
                    id="rainfall"
                    name="rainfall"
                    min={0}
                    max={371}
                    value={rainfall}
                    onChange={(e) =>
                      setRainfall(Math.max(0, Math.min(371, parseInt(e.target.value || 0))))
                    }
                    className="block w-32 px-2 py-1 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  />
                  <span className="text-gray-600">Current: {rainfall}</span>
                </div>
              </div>

              {/* Humidity3pm (spinner 0–45) */}
              <div>
                <label
                  htmlFor="humidity3pm"
                  className="block text-sm font-medium text-gray-700"
                >
                  Humidity at 3pm (0–45)
                </label>
                <div className="mt-1 flex items-center gap-4">
                  <input
                    type="number"
                    id="humidity3pm"
                    name="humidity3pm"
                    min={0}
                    max={45}
                    value={humidity3pm}
                    onChange={(e) =>
                      setHumidity3pm(Math.max(0, Math.min(45, parseInt(e.target.value || 0))))
                    }
                    className="block w-32 px-2 py-1 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  />
                  <span className="text-gray-600">Current: {humidity3pm}</span>
                </div>
              </div>

              {/* Rain Today (checkbox) */}
              <div className="flex items-center">
                <input
                  type="checkbox"
                  id="rain-today"
                  name="rainToday"
                  checked={rainToday}
                  onChange={(e) => setRainToday(e.target.checked)}
                  className="h-5 w-5 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                />
                <label
                  htmlFor="rain-today"
                  className="ml-2 block text-sm font-medium text-gray-700"
                >
                  Rain Today
                </label>
              </div>

              {/* Cloud3pm (spinner 0–8) */}
              <div>
                <label
                  htmlFor="cloud3pm"
                  className="block text-sm font-medium text-gray-700"
                >
                  Cloud Level at 3pm (0–8)
                </label>
                <div className="mt-1 flex items-center gap-4">
                  <input
                    type="number"
                    id="cloud3pm"
                    name="cloud3pm"
                    min={0}
                    max={8}
                    value={cloud3pm}
                    onChange={(e) =>
                      setCloud3pm(Math.max(0, Math.min(8, parseInt(e.target.value || 0))))
                    }
                    className="block w-32 px-2 py-1 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  />
                  <span className="text-gray-600">Current: {cloud3pm}</span>
                </div>
              </div>

              {/* Sunshine (spinner 0–17) */}
              <div>
                <label
                  htmlFor="sunshine"
                  className="block text-sm font-medium text-gray-700"
                >
                  Sunshine (0–17)
                </label>
                <div className="mt-1 flex items-center gap-4">
                  <input
                    type="number"
                    id="sunshine"
                    name="sunshine"
                    min={0}
                    max={17}
                    value={sunshine}
                    onChange={(e) =>
                      setSunshine(Math.max(0, Math.min(17, parseInt(e.target.value || 0))))
                    }
                    className="block w-32 px-2 py-1 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  />
                  <span className="text-gray-600">Current: {sunshine}</span>
                </div>
              </div>

              {/* Submit button */}
              <div>
                <button
                  type="submit"
                  disabled={isLoading}
                  className={`w-full flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-white ${
                    isLoading ? "bg-gray-400" : "bg-blue-600 hover:bg-blue-700"
                  } focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500`}
                >
                  {isLoading ? "Predicting..." : "Submit for Prediction"}
                </button>
              </div>

              {/* Display error or API response */}
              {errorMsg && (
                <p className="text-red-600 text-sm text-center">
                  {errorMsg}
                </p>
              )}
              {apiResponse && (
                <div className="bg-green-50 border-l-4 border-green-400 p-4">
                  <p className="text-green-700 font-medium">
                    Prediction result:
                  </p>
                  <pre className="mt-2 text-sm text-gray-700">
                    {JSON.stringify(apiResponse, null, 2)}
                  </pre>
                </div>
              )}
            </form>
          </div>

          {/* ─── RIGHT COLUMN: PLACEHOLDER OR ADDITIONAL INFO ──────────────── */}
          <div className="flex flex-col justify-center items-center bg-gray-50 border border-gray-200 rounded-lg p-6">
            <h2 className="text-xl font-semibold mb-4">RISK MM</h2>
            <p className="text-gray-600 text-center">
              This panel can show additional metrics, charts, or instructions.
              For now, it’s a placeholder titled “RISK MM”.
            </p>
          </div>
        </div>
      </main>

      {/* Footer (empty for now) */}
      <footer className="bg-gray-800 text-white py-4 text-center">
        &copy; {new Date().getFullYear()} Your Company
      </footer>
    </div>
  );
}
